.section-eighth {
  background-color: #212121;
  height: 680px;
  margin: auto;
  width: 1280px; }

.section-eighth-content {
  height: 500px;
  margin: auto;
  width: 1080px;
  display: flex;
  justify-content: space-between; }

.section-eighth-map {
  height: 480px;
  width: 640px; }

.section-eighth-feedback-form {
  height: 390px;
  width: 360px; }
  .section-eighth-feedback-form ul {
    color: #FFFFFF; }
    .section-eighth-feedback-form ul li:nth-child(1) {
      font-family: 'e-Ukraine';
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 150%;
      text-transform: uppercase; }
    .section-eighth-feedback-form ul li:nth-child(2) {
      font-family: 'e-Ukraine';
      font-style: normal;
      font-weight: 100;
      font-size: 18px;
      line-height: 150%; }

.section-eighth-footer-nav {
  height: 90px;
  width: 1080px;
  margin: auto;
  display: flex;
  justify-content: space-between; }

.section-eighth-bottom-bar {
  background-color: #212121;
  height: 40px;
  margin: auto;
  width: 1280px;
  color: #FFFFFF; }
  .section-eighth-bottom-bar p {
    margin: 55px 0 0 40px;
    padding: 12px 0 0 0; }

.footer-nav__logo {
  height: 90px;
  width: 125px;
  display: flex;
  justify-content: center;
  align-items: center; }

.footer-nav__nav1 {
  height: 90px;
  width: 125px; }
  .footer-nav__nav1 ul {
    display: grid;
    gap: 20px; }
    .footer-nav__nav1 ul li {
      display: block;
      color: #FFFFFF;
      text-decoration: none; }

.footer-nav__nav2 {
  height: 90px;
  width: 125px; }
  .footer-nav__nav2 ul {
    display: grid;
    gap: 20px; }
    .footer-nav__nav2 ul li {
      display: block;
      color: #FFFFFF;
      text-decoration: none; }

.footer-nav__contact {
  height: 90px;
  width: 325px; }
  .footer-nav__contact ul {
    display: grid;
    gap: 20px; }
    .footer-nav__contact ul li {
      color: #FFFFFF; }

.footer-nav__icons {
  height: 90px;
  width: 125px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.feedback-form {
  display: flex;
  height: 285px;
  align-items: end; }
  .feedback-form form {
    height: 230px;
    display: grid; }
  .feedback-form .input-field:hover {
    color: #FFFFFF; }
  .feedback-form .input-field:active {
    border: 1px solid #009688; }
  .feedback-form .request-a-call-button:hover {
    background: #00796B;
    border: 1px solid #00796B; }
  .feedback-form .request-a-call-button:active {
    background: #00796B;
    box-shadow: inset 0px 7px 10px rgba(33, 33, 33, 0.54);
    border: 1px solid #00796B; }
