.section-seventh {
  background-color: #212121;
  height: 1340px;
  margin: auto;
  width: 1280px; }

.content-blog {
  margin: auto;
  width: 1122px;
  height: 1223px;
  color: #FFFFFF; }

.content-blog-title {
  width: 1122px;
  height: 90px; }
  .content-blog-title > p {
    font-family: 'e-Ukraine Head';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 125%;
    text-transform: uppercase; }

.content-blog__description {
  display: flex;
  justify-content: space-between; }
  .content-blog__description p {
    font-family: 'e-Ukraine';
    font-style: normal;
    font-weight: 200;
    font-size: 16px;
    line-height: 150%; }

.content-blog__go-to-blog a {
  font-family: 'e-Ukraine';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #009688;
  text-decoration: none; }

.content-blog-card {
  width: 1120px;
  height: 295px;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #F5F5F5;
  padding: 30px 0 0 0; }

.content-card__title {
  width: 360px;
  height: 265px;
  display: grid;
  align-content: space-between;
  font-family: 'e-Ukraine';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%; }
  .content-card__title p:nth-child(2) {
    font-family: 'e-Ukraine';
    font-style: normal;
    font-weight: 200;
    font-size: 12px;
    line-height: 150%; }

.content-card__img {
  width: 360px;
  height: 265px; }

.content-card__discription {
  width: 353px;
  height: 260px;
  display: grid;
  align-content: space-between;
  font-family: 'e-Ukraine';
  font-style: normal;
  font-weight: 200;
  font-size: 16px;
  line-height: 150%; }
  .content-card__discription img {
    margin: 0 0 0 300px;
    cursor: pointer; }

.section-seventh-content-card__btn {
  display: flex;
  justify-content: center;
  width: 1122px;
  height: 90px;
  margin: auto;
  border-top: 1px solid #F5F5F5; }
  .section-seventh-content-card__btn .download-more-button {
    margin: 60px 0 0 0; }
    .section-seventh-content-card__btn .download-more-button:hover {
      border: 1px solid #00796B; }
    .section-seventh-content-card__btn .download-more-button:active {
      background: rgba(255, 255, 255, 0.1);
      border: 1px solid #00796B;
      box-shadow: inset 0px 7px 10px rgba(33, 33, 33, 0.54); }
