.section-header {
  position: fixed;
  margin: auto;
  top: 0px;
  z-index: 1;
  display: flex;
  width: 1280px;
  height: 50px;
  background: rgba(0, 0, 0, 0.3);
  border: 0.1px solid #FFFFFF;
  justify-content: space-between;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px); }

.header-logo {
  margin: 12px 0 0 93px; }

.header-link {
  width: auto;
  display: flex;
  justify-content: space-around; }
  .header-link ul {
    display: flex;
    justify-content: space-between;
    width: 560px;
    text-decoration: none; }
    .header-link ul a {
      text-decoration: none;
      display: flex; }
  .header-link li {
    font-family: 'e-Ukraine';
    font-style: normal;
    font-weight: 200;
    font-size: 14px;
    line-height: 130%;
    text-transform: uppercase;
    color: #FFFFFF;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.header-basket-search {
  margin: 12px 80px 0 0px; }
  .header-basket-search img {
    margin: 0 0 0 20px; }
