.section-three {
  background-color: #212121;
  width: 1280px;
  height: 1280px;
  margin: auto; }

.section-three__title {
  display: flex;
  margin: 0 0 0 80px;
  font-family: 'e-Ukraine Head';
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 125%;
  text-transform: uppercase;
  color: #FFFFFF; }
  .section-three__title span {
    margin: 15px 0 0 100px;
    display: flex;
    width: 835px;
    height: 1px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.4); }

.content-table {
  display: grid;
  grid-auto-rows: minmax(100px, auto);
  margin: 0 0 20px 50px; }

.content-card {
  width: 360px;
  height: 465px;
  margin: 15px 0px 15px 0px;
  color: #FFFFFF; }
  .content-card p {
    margin: 20px 0 20px 0; }

.more-button:hover {
  background: #00796B;
  border: 1px solid #00796B; }

.more-button:active {
  box-shadow: inset 0px 7px 10px rgba(33, 33, 33, 0.54);
  border: 1px solid #00796B; }

.content-card-img-blur {
  width: 360px;
  height: 50px;
  background: rgba(106, 106, 106, 0.2);
  border: 0.1px solid #FFFFFF;
  backdrop-filter: blur(4px);
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 0 0 0 10px; }

.content-card__btn {
  display: flex;
  justify-content: end; }
  .content-card__btn p {
    margin: 10px 287px 0 0px; }

.card-1 {
  background-image: url(/classwork/lending_name/dist/images/popular-card-1.png);
  background-repeat: no-repeat;
  grid-column: 1;
  grid-row: 1; }

.card-2 {
  background-image: url(/classwork/lending_name/dist/images/popular-card-2.png);
  background-repeat: no-repeat;
  grid-column: 2;
  grid-row: 1; }

.card-3 {
  background-image: url(/classwork/lending_name/dist/images/popular-card-3.png);
  background-repeat: no-repeat;
  grid-column: 3;
  grid-row: 1; }

.card-4 {
  background-image: url(/classwork/lending_name/dist/images/popular-card-4.png);
  background-repeat: no-repeat;
  grid-column: 1;
  grid-row: 2; }

.card-5 {
  background-image: url(/classwork/lending_name/dist/images/popular-card-5.png);
  background-repeat: no-repeat;
  grid-column: 2;
  grid-row: 2; }

.card-6 {
  background-image: url(/classwork/lending_name/dist/images/popular-card-6.png);
  background-repeat: no-repeat;
  grid-column: 3;
  grid-row: 2; }

.content-card-img {
  width: 360px;
  height: 360px;
  display: flex;
  justify-content: end;
  align-items: flex-end; }

.section-three__btn {
  display: flex;
  justify-content: center;
  margin: 60px 0 0 0; }
  .section-three__btn .download-more-button:hover {
    border: 1px solid #00796B; }
  .section-three__btn .download-more-button:active {
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid #00796B;
    box-shadow: inset 0px 7px 10px rgba(33, 33, 33, 0.54); }
